@import './color-palette';

// (neutral)
@neutral-1: #fff;
@neutral-2: #f7f7f7;  // 
@neutral-3: #efefef;
@neutral-4: #e9e9e9;  // 
@neutral-5: #d9d9d9;  // 
@neutral-6: #ccc;     // 
@neutral-7: #999;     // 
@neutral-8: #666;     // 
@neutral-9: #404040;  // 
@neutral-10: #222;
@neutral-11: #000;

// brand
@brand-1: @blue-1;
@brand-2: @blue-2;
@brand-3: @blue-3;
@brand-4: @blue-4;
@brand-5: @blue-5;
@brand-6: @blue-6;    // 
@brand-7: @blue-7;    // /
@brand-8: @blue-8;
@brand-9: @blue-9;
@brand-10: @blue-10;

// function
@function-error-1: @orange-1;  //  
@function-error-2: @orange-6;  // 

@function-success-1: @green-1;  //  
@function-success-2: @green-6;  // 

@function-warning-1: @yellow-1;  //  
@function-warning-2: @yellow-6;  // 

@function-link-1: @brand-6;  // 

// 
@font-family-1: -apple-system,"SF UI Text",Roboto,Noto,"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
@font-family-2: Consolas,Menlo,Courier,monospace;

// 
@font-size-1: 10px;
@font-size-2: 12px;
@font-size-3: 13px;
@font-size-4: 14px;
@font-size-5: 16px;
@font-size-6: 18px;
@font-size-7: 20px;

// 
@grid: 4px;

// emdp
@line-height-1: 1em;
@line-height-2: 1.5em;

// 
@font-weight-1: normal;
@font-weight-2: bold;

//  (radius)
@radius-1: 4px;
@radius-2: 6px;

// 
@ease-in-out-quint: cubic-bezier(0.86, 0, 0.07, 1);

// Shadow
@shadow-color           : rgba(100, 100, 100, .2);
@shadow-1-right         : 1px 0 6px @shadow-color;



